// Preloader
%preloader-svg{
    align-items: center;
    justify-content: center;
    svg{
        > g{
            stroke-dasharray: 50 40;
            stroke-dashoffset: 50;
            stroke-linecap: square;
            fill: none;
            transform-origin: center;
            stroke-width: 5px;
            stroke: $c-primary;
            animation: preloader .6s linear infinite;
        }
    }
}

.preloader{
    position: fixed;
    top: 0;
    left: 0;
    z-index: $z-master;
    background-color: rgba($color: $c-white, $alpha: .4);
    display: none;
    height: 100vh;
    width: 100%;
    @extend %preloader-svg; 
}

.preloader-inline{
    display: flex;
    padding: 20px;
    @extend %preloader-svg;
}

.page-processing{
    .preloader{
        display: flex;
    }
}