        }
        &-products{
            margin-top: 15px;
            .prd-row-inner{
                margin: 0;
                border-bottom: 1px solid $c-grey-light;
            }
        }
    }
    &-downloads{
        padding-top: 20px;
        .menu-btn{
            padding: 0;
