// Login & Signup
.signing{
    display: flex;
}
.sgn-holder{
    width: 635px;
    position: relative;
    .signup{
        height: 100%;
    }
}
.sgn-media{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    min-height: 750px;
    flex: 1;
}

// Login
.login{
    padding: 80px 0;

    h1{
        @include font-set(h-large, true);
        margin-bottom: 40px;
    }
}

// Signup
.signup{
    padding: 80px 0;

    .prg{
        &-header{
            position: absolute;
            z-index: $z-lower;
            background-color: $c-bg-light;
        }
        &-title, &-inner::after, &-inner::before{
            display: none;
        }
        &-back{
            button span{
                display: inline;
            }
        }
        &-inner{
            padding: 30px 50px;
        }
    }
}

// Guest
.guest{
    padding: 80px 0;
    .prg{
        &-header{
            position: absolute;
            z-index: $z-lower;
            background-color: $c-bg-light;
        }
        &-title, &-inner::before{
            display: none;
        }
        &-back{
            button span{
                display: inline;
            }
        }
        &-inner{
            padding: 30px 50px;
        }
    }
}

// Social OTP
