// Category Page
.brand-list{
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, minmax(auto, 165px));
    grid-template-rows: auto;
    gap: 5px;

    li{
        height: 165px;
        @include responsive($w: 300, $h: 160);
        
        a{
            display: flex;
            align-items: center;
            background-color: $c-bg-light;
            justify-content: center;
            height: 100%;
        }
    }
}

.category-list{
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, minmax(auto, 165px));
    gap: 5px;

    li{
        text-align: center;
    }
}

.category-cover{
    height: 375px;
}


// Product List Page
.top-panel {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px $container-padding-x;
    > div {
        display: flex;
        align-items: center;
    }
    .pnl {
        &-left {
            margin-right: auto;
        }
        &-middle {
            width: 100%;
            margin-top: 20px;
        }
        &-right {
            margin-left: auto;
        }
    }
    h1 {
        @include font-set(h);
        margin: 0;
    }
}

.primary-filters {
    margin: 0 (-$container-padding-x);
    padding: 0 15px;
    > a {
        flex: none;
        margin: 0 5px;
        padding: 0 15px;
    }
}

.btn-filter {
    display: flex;
    align-items: center;
    margin-left: 5px;
    line-height: 1;
    // > svg {
    //     margin-left: 10px;
    // }
}

.btn-sort {
    display: flex;
    align-items: center;
    line-height: 1;
    .icon{
        margin-left: 10px;
        font-size: 14px;
    }
}

.products {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(auto, 355px));
    grid-auto-flow: dense;
    gap: 30px;
    justify-content: center;
    padding: 0 10px;
}

// Product Scroller
.product-scroll{
    display: block;
    padding: 2px 15px;
    
    .prd{
        display: inline-block;
        max-width: 315px;

        & + .prd{
            margin-left: 10px;
        }
    }
}



.compare-info {
    display: none;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: $z-normal;
    width: 100%;
    height: 60px;
    padding: 0 $container-padding-x;
    animation: toTop .32s ease;
    background: $c-white;

    .btn-compare {
        padding: 0 30px 0 40px;
        .qty-badge {
            background: $c-white;
            color: $c-black;
        }
    }
    
    .compare-activated & {
        display: flex;
    }
}

.category-explorer {
    $i: 80px;
    display: none;
    align-items: center;
    margin: 30px auto;
    padding-top: $i;
    > a {
        flex: 1;
        background: $c-light;
        display: flex;
        align-items: center;
        @include transitions();
    }
    figure {
        width: 240px;
        margin-top: -$i;
        @include transitions($func: 'eut', $dur: 'faster');
    }
    .title {
        margin: 5px 0 0 0;
        @include font-set(h);
    }

    .prev {
        justify-content: flex-start;
    }
    .next {
        border-left: 4px solid $body-bg;
        justify-content: flex-end;
    }
}

// Assistant
.page-product-list{
    #assistant{
        opacity: 1;
        @include transitions();

        .bnr-info{
            display: inline-flex;
            align-items: center;

            //pulse dot
            @at-root {
                .quiz{
                    flex: none;
                    display: inline-block;
                    position: relative;
                    margin-right: 6px;
                    width: 20px;
                    height: 20px;
                    
                    &::before{
                        content: "";
                        display: inline-block;
                        width: 5px;
                        height: 5px;
                        border-radius: 50%;
                        background-color: $c-primary;
                        position: absolute;
                        top: calc(50% - 2.2px);
                        left: calc(50% - 2.6px);
                        animation: beat 1.4s linear; animation-iteration-count: infinite;
                    }
    
                    &::after{
                        content: "";
                        display: inline-block;
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        border: 1px solid $c-primary;
                        animation: pulse 1.4s linear; animation-iteration-count: infinite;
                    }
                }
            }
        }

        .bnr-button{
            display: flex;

            .btn{
                flex: 1;
                padding: 0 20px;
            }
        }

        .bnr-footer{
            margin-top: 25px;
            text-align: center;
        }

        .bnr-media{
            display: none;
        }
    }
}

.products{
    #assistant{
        .bnr{
            &-iframe{
                display:none;
            }
        }
    }
}

