// Live Chat
#live-chat{
    margin: 20px auto;
    padding: 0 10px;
}

.lcs{
    background-color: $c-bg-live-chat;
    padding: 20px 42px 13px 13px;
    position: relative;
    .btn-close{
        position: absolute;
        top: 50%;
        right: 10px;
        color: $c-text-light;
        transform: translateY(-50%);
    }
    &-inner{
        padding-left: 100px;
        position: relative;
    }
    &-media{
        @include responsive(105, 37);
        width: 85px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    &-info{
        margin-bottom: 10px;
        color: $c-text-light;
    }
    &-button{
        i{
            display: block;
            height: 12px;
            width: 12px;
            border-radius: 100%;
            background-color: $c-online;
            left: 10px;
        }
    }
}

// Category
.page-category{
    .tab-content{
        padding: 30px 20px 50px;
    }
}

// Promotion List
.page-promotion-list{
    .banner{
        margin-bottom: 5px;

        ul{
            padding-left: 15px;
            margin: 10px 0 20px;
        }
    }
}

// Promotion User OTP
.otpromo{
    text-align: left !important;
    display: flex;
    width: calc(100% + 5px);
    margin: 0 0 50px -5px;
    &-block1{
        padding: 0 8px 0 0;
        .icon{
            color: $c-primary;
        }
    }
    &-title{
        @include font-set(h-small);
    }
    &-text{
        margin-top: 20px;
    }
    &-form{
        margin-top: 20px;
        display: flex;
        align-items: center;
        &-input{
            margin-right: 20px;
        }
    }
    &-info{
        margin-top: 20px;
        @include font-set(body-small);
    }
}

// Promotion Detail
.page-promotion-detail{
    .bnr-promo{
        padding: 30px 0 95px;
    }
}

// Compare Page
.page-compare {
    background: $c-bg-light;
    #site-main {
        padding-bottom: 100px;
    }
}
.compare-search-ready {
    .suggested-searches {
        display: none;
    }
    .suggested-results {
        display: block;
    }
}
.compare-holder{
    &.title-block{
        .compare-top-panel{
            background-color: $c-bg-body;
        }
    }
    .swiper-slide{
        width: 160px;
        + .swiper-slide{
            margin-left: 5px;
        }
        &:first-child{
            margin-left: 15px;
        }
        &:last-child{
            margin-right: 15px;
        }
    }
    .swiper-compare {
        &-prev,
        &-next {
          position: fixed;
          top: 50%;
          z-index: $z-lower;
          width: 32px;
          height: 32px;
          margin-top: -16px;
          @extend .svg;
          @extend .svg-swiper-next-dark;
          outline: none;
          display: none;
        }
        &-prev {
          left: calc(((100vw - 16px - 880px) / 2) - 60px);
          transform: scale(-1);
        }
        &-next {
          right: calc(((100vw - 16px - 880px) / 2) - 60px);
        }
    }
}
.compare-top-panel {
    position: sticky;
    top: $header-height-mobile - 20px;
    z-index: $z-low;
    padding-top: 20px;

    .swiper-pagination {
        position: absolute;
        margin-top: 0;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
    }
}
.cmp-item {
    border-top: 3px solid $c-primary;
    background: $c-white;
    display: flex;
    align-items: center;
    border-bottom: 1px solid $c-bg-light;
    .cmp-prd-title {
        position: relative;
        padding: 10px;
        font-weight: $fw-bold;
        flex: 1;
        height: 60px;
        > span {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
    }
    .cmp-remove{
        flex: 0 0 32px;
        margin-right: 10px;
    }
    // button.cmp-prd-title{
    //     &:after {
    //         @extend .svg;
    //         @extend .svg-more;
    //         content: "";
    //         position: absolute;
    //         right: 0;
    //         top: 50%;
    //         transform: translateY(-50%);
    //     }
    // }
}

.cmp-add{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $cmp-add-bg-color;
    text-align: center;
    width: 100%;
    height: 235px;
    span{
        font-weight: $fw-bold;
        display: block;
    }
    .icon{
        margin-bottom: 15px;
        font-size: 80px;
        color: $cmp-add-icon-color;
    }
    &-inner{
        transform: translateY(-32px);
    }
    &-header{
        height: 64px;
        background-color: $cmp-add-bg-color;
        .cmp-add{
            height: 100%;
            width: 100%;
        }
    }
}

#compare-products, #compare-all-features{
    .swiper-pagination {
        display: none;
    }
}

.compare-primary-features {
    margin-top: 5px;
    padding: 0 10px;
    background: $c-white;
    .item {
        position: relative;
        text-align: center;
        padding: 20px 10px;
        + .item {
            border-top: 1px solid $c-bg-light;
        }
        &.no-attr{
            figure span{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                text-align: center;
                font-size: 24px;
            }
            .t{
                visibility: hidden;
            }
        } 
    }
    .prd-reviews {
        justify-content: center;
    }
    .responsive {
        max-width: 32px;
        margin: auto;
    }
    .t {
        margin-top: 10px;
    }
    .qm {
        position: absolute;
        top: 10px;
        right: 0;
    }
}

#compare-all-features {
    .acc-item {
        margin: 0 $container-padding-x;
    }
    .item {
        position: relative;
        margin-right: 20px;
        + .item {
            margin-top: 20px;
        }
        .qm {
            vertical-align: middle;
        }
        &.same{
            display: none;
        }
        &.no-attr .t{
            visibility: hidden;
        }
    }
}
.btn-open-acc-items {
    text-decoration: underline;
    margin: 20px auto;
    display: block;
}

.compare-bottom-panel {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: $z-low;
    width: 100%;
    background: $c-white;
}

// Search Results Page
.page-search-results {
    #site-main {
        padding-bottom: 60px;
    }
    .page-title {
        text-align: left;
        padding: 0;
        h1 {
            @include font-set(h-small, true);
        }
    }
    .nav {
        margin-top: 10px;
        width: auto;
        &-link {
            flex: 1;
        }
    }
    .tab-content {
        padding: 0;
    }
    .no-result{
        padding: 100px 0;
        text-align: center;
        .icon{
            margin-bottom: 10px;
            color: $c-text-dark;
        }
    }
}



// Product Detail Page
.page-product-detail{
    #site-footer{
        margin-top: 0;
    }
}

.pdp {
    background: linear-gradient(to bottom, $c-bg-pdp-details-1, $c-bg-pdp-details-2 25%);
    color: $c-white;
    padding-top: $header-height-mobile;
    &-breadcrump{
        display: flex;
        width: 100%;
        .breadcrumb {
            padding-top: 4px;
            padding-bottom: 4px;
            li::after{
                @extend .svg-arrow2-r-w;
            }
        }
        .btn-more{
            margin-left: auto;
            margin-right: 15px;
        }
    }
    &-more {
        position: relative;
        z-index: $z-low + 1;
        display: none;
    }
    &-gallery {
        width: 100%;
        position: relative;
        .swiper-button-prev,
        .swiper-button-next {
            display: none;
        }
        .swiper-slide {
            padding: 0 15px;
            img{
                width: 100%;
                height: 100%;
            }
        }
        &-360{
            display: none;
        }
    }
    &-ar {
        height: 0;
        text-align: center;
        overflow: hidden;
        margin-top: 15px;
        @include transitions($dur: 'normal');
        @at-root {
            #btn-ar{
                padding-right: 25px;
                justify-content: end;
                span{
                    visibility: visible;
                    white-space: nowrap;
                }
                &::before{
                    display: none;
                }
                .icon {
                    color: $c-primary;
                }
                .stroke{
                    position: absolute;
                    top: -1px;
                    left: -1px;
                    width: $btn-h-default;
                    height: $btn-h-default;
                    circle{
                        stroke-dasharray: 30 270;
                        stroke-dashoffset: 0;
                        fill: none;
                        transform-origin: center;
                        stroke-width: 3px;
                        stroke: $c-primary;
                        animation: rotating 1s linear infinite;
                    }
                }
            }
            .ar-button-ready{
                .pdp-ar{
                    height: 55px;
                }
                #btn-ar{
                    animation: pdpAR 3s ease forwards;
                }
            }
        }
    }
    &-360 {
        display: none;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: $z-lower + 1;
        @at-root {
            .btn-360 {
                background: $c-white;
                color: $body-color;
                border-radius: 50%;
                padding: 9px;
            }
        }
    }
    &-details {
        padding: 0 $container-padding-x;
    }
    &-energy-label {
        vertical-align: super;
        margin-right: 4px;
        display: inline-block;
    }
    &-name {
        padding: 0 20px;
        margin-top: 15px;
        text-align: center;
        line-height: 30px;
        .title {
            @include font-set(h);
            margin: 0;
            display: inline;
            line-height: 30px;
        }
    }
    &-reviews {
        margin-top: 5px;
        justify-content: center;
    }
    &-sizes {
        margin-top: 20px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        a {
            display: inline-block;
            min-width: 55px;
            padding: 8px 5px;
            border: 1px solid currentColor;
            color: $c-grey-dark;
            text-align: center;
            margin: 2.5px;
            &.selected {
                background: $c-light;
                color: $c-dark;
                border-color: $c-light;
            }
        }
    }
    &-colors {
        margin-top: 20px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        a {
            border: 1px solid transparent;
            border-radius: 50%;
            padding: 4px;
            margin: 2.5px;
            &::before {
                content: "";
                display: block;
                border: 10px solid currentColor;
                border-radius: 50%;
            }
            &.selected {
                border-color: $c-light;
            }
        }
    }
    &-warranty{
        margin-top: 20px;
        border-top: 1px solid $c-pdp-border;
        @at-root {
            .btn-warranty {
                display: flex;
                align-items: center;
                width: 100%;
                padding: 8px 0;
                .icon-plus {
                    margin-left: auto;
                }
            }
        }
    }
    &-installation{
        border-top: 1px solid $c-pdp-border;
        @at-root {
            .btn-installation {
                display: flex;
                align-items: center;
                width: 100%;
                padding: 8px 0;
                .icon-plus {
                    margin-left: auto;
                }
            }
        }
    }
    &-purchase {
        // height: 126px;
        padding: 10px 0 15px;
        border-top: 1px solid $c-pdp-border;
        &-inner {
            display: flex;
            align-items: center;
            .left {
                flex: 1;
                padding-right: 30px;
            }
        }

        $pl: 50px;
        &-fixed {
            .btn-switch {
                display: none;
                position: absolute;
                top: 18px;
                left: 10px;
                @include transitions();
            }
            .t {
                display: none;
                position: absolute;
                top: 15px;
                left: $pl;
                width: 50%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                @include font-set(body-small);
                @include transitions();
            }
        }

        &-ready {
            .pdp{
                .pdp-purchase-fixed {
                    position: fixed;
                    top: 0;
                    left: 0;
                    z-index: $z-high;
                    width: 100%;
                    background: $c-bg-body;
                    color: $body-color;
                    padding: 15px 20px 11px $pl;
                    .btn-switch{
                        display: block;
                    }
                    .left{
                        padding-right: 10px;
                    }
                }
                .pdp-purchase-inner {
                    align-items: flex-end;
                }
                .pdp-add-to-cart{
                    max-width: 205px;
                }
                // .pdp-badge1 {
                //     display: none;
                // }
            }
            @at-root {
                .compare-search-ready .pdp-purchase-fixed{
                    z-index: $z-supnormal !important;
                }
            }
        }

        &-opened {
            overflow: hidden;
            .pdp{
                .pdp-purchase-fixed {
                    padding: 18px 20px 30px;
                    .btn-switch {
                        transform: scale(-1);
                    }
                    .t {
                        display: block;
                        top: 29px;
                        width: calc(100% - #{$pl} - 20px);
                    }
                    .pdp-tab-steps {
                        display: block;
                    }
                    .pdp-purchase-inner {
                        align-items: center;
                        padding-top: 20px;
                        border-top: 1px solid $c-grey-border;
                    }
                    // .pdp-badge1 {
                    //     display: flex;
                    // }
                }
            }
            #overlay {
                display: block;
                z-index: $z-high - 1;
            }
        }

        &.out-of-stock{
            .left{
                display: none;
            }
            .pdp-add-to-cart{
                width: 70%;
                margin: 0 auto;
                .btn{
                    width: 100%;
                }
            }

            @at-root {
                .pdp-purchase-ready{
                    .pdp-purchase.out-of-stock{
                        .pdp-add-to-cart{
                            visibility: hidden;
                        }
                    }
                }
                .pdp-purchase-opened{
                    .pdp-purchase.out-of-stock{
                        .pdp-purchase-inner{
                            display: none;
                        }
                    }
                }
            }
        }
    }
    &-badge1 {
        display: flex;
        align-items: center;
        margin-bottom: 25px;
        > .responsive {
            flex: none;
            margin-right: 5px;
            max-width: 32px;
        }
        > span {
            @include font-set(body-small);
        }
    }
    &-badge2{
        margin-top: 2px;
        .prc-third{
            &:not(:empty){
                font-weight: $fw-bold;
            }
            &-info:not(:empty){
                padding-left: 4px;
                @include font-set(body-small);
            }
        }
    }
    &-price {
        // @include font-set(body-large);
        height: 14px;
        .prc {
            &-first {
                color: $c-grey-dark;
                margin-right: 10px;
            }
            // &-last {
            //     font-weight: $fw-bold;
            // }
        }
    }
    &-add-to-cart {
        flex: none;
        margin-left: auto;
        .btn {
            padding: 0 20px;
        }
    }
    &-info1 {
        margin-top: 15px;
        border-top: 1px solid $c-pdp-border;
        border-bottom: 1px solid $c-pdp-border;
        @at-root {
            .btn-info1 {
                display: flex;
                align-items: center;
                width: 100%;
                background: rgba($color: $c-grey-dark, $alpha: .4);
                padding: 14px 20px;
                .icon-clock {
                    margin-right: 8px;
                }
                .icon-arrow-b-small {
                    transform: rotate(-90deg);
                    margin-left: auto;
                }
            }
        }
    }
    &-benefits {
        margin: 30px (-$container-padding-x) 0;
        .swiper-button-prev,
        .swiper-button-next,
        .swiper-pagination {
            display: none;
        }
        .swiper-slide {
            width: 140px;
            + .swiper-slide {
                margin-left: 10px;
            }
            &:first-child {
                margin-left: 20px;
            }
            &:last-child {
                margin-right: 20px;
            }
        }
        .item {
            display: flex;
            align-items: center;
            height: 70px;
            overflow: hidden;
            padding: 10px 15px;
            border: 1px solid $c-pdp-border;
            .icon {
                margin-right: 10px;
            }
            &-important {
                background: $c-grey-dark;
            }
        }
    }
    &-features {
        margin-top: 30px;
        padding-bottom: 50px;
        .item {
            display: flex;
            align-items: center;
            .responsive {
                width: 75px;
                margin-right: 10px;
                flex: none;
            }
            .v {
                @include font-set(h);
            }
            .t {
                margin-top: 10px;
            }

            + .item {
                margin-top: 30px;
            }

            &.brd {
                padding-top: 30px;
                border-top: 1px solid $c-pdp-border;
            }
        }
    }
    &-bottom {
        display: none;
        position: sticky;
        bottom: 0;
        z-index: $z-low;
        border-top: 1px solid $header-top-cover-border-bottom-color;
        height: 70px;
