    &-downloads {
        margin-top: 30px;
        .nav-link {
            flex: 1;
        }
        .item {
            a {
                display: flex;
                align-items: center;
                padding: 15px 0;
                border-top: 1px solid $c-grey-border;
                &::after {
                    @extend .svg;
                    @extend .svg-download;
                    content: "";
                    display: block;
                    margin-left: auto;
                    flex: none;
                }
            }
            .v {
                padding-right: 30px;
            }
        }
    }
    &-dimensions {
        &-graph {
            $p: 25px;
            position: relative;
            padding: $p 0 0 $p;
            [class*="ruler-"] {
                position: absolute;
                top: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                .t {
                    position: relative;
                    background: $c-bg-light;
                    padding: 0 15px;
                }
                &::before {
                    content: "";
                    position: absolute;
                }
            }
            .ruler {
                &-h {
                    width: 50%;
                    left: 25%;
                    &::before {
                        border-top: 1px solid $c-grey-border;
                        top: 50%;
                        left: 0;
                        width: 100%;
                    }
                }
                &-v {
                    height: 50%;
                    top: 25%;
                    left: -$p;
                    .t {
                        transform: rotate(-90deg);
                    }
                    &::before {
                        border-left: 1px solid $c-grey-border;
                        top: 0;
                        left: 50%;
                        height: 100%;
                    }
                }
            }
        }
        &-list {
            margin-top: 10px;
            display: flex;
            justify-content: center;
            .item {
                text-align: center;
                .t {
                    margin-top: 5px;
                    color: $c-grey-dark;
                }
                + .item {
                    margin-left: 40px;
                }
            }
        }
    }
    &-store-locator{
        .srv{
            &-list{
                margin: 0 0 70px;
            }
            &-item{
                background-color: $c-bg-body;
            }
            &-address{
                margin-top: 3px;
                padding-right: 50px;
            }
        }
    }

    // Mini View
    &-mini {
        $pr: 200px;
        overflow: hidden;
        &-block1 {
            position: relative;
            padding: 70px $pr 30px $container-padding-x;
            margin: 0 (-$container-padding-x);
        }
        &-container{
            background: $c-bg-colored;
            padding: 0 20px;
        }
        &-media {
            position: absolute;
            top: 0;
            right: -120px;
            z-index: $z-lower;
            width: 320px;
        }
        &-name {
            .title {
                @include font-set(h);
            }
        }
        &-cat {
            margin-top: 5px;
            display: flex;
            align-items: center;
        }
        &-energy-label {
            margin-right: 8px;
        }
        &-features {
            margin: 20px auto 0;
            min-height: 160px;
            padding: 0 $pr 0 20px;
            ul {
                padding-left: 15px;
            }
            li{
                margin-bottom: 10px;
            }
        }
        &-warranty {
            &-bar {
                $bar-h: 6px;
                $bar-thumb: 10px;
                position: relative;
                background: linear-gradient(to right, #6e994e 49%, #b32424);
                height: $bar-h;
                > i {
                    position: absolute;
                    top: ($bar-thumb - $bar-h) / -2;
                    width: $bar-thumb;
                    height: $bar-thumb;
                    border: 1px solid $body-color;
                    border-radius: 50%;
                    background: $c-bg-body;
                }
            }

            &-status {
                position: relative;
                margin-top: 15px;
                padding-left: 14px;
                > small {
                    display: block;
                    margin-top: 3px;
                    @include font-set(body-small);
                    color: $c-grey-dark;
                }
                &::before {
                    position: absolute;
                    top: 3px;
                    left: 0;
                }
            }

            @at-root {
                .btn-warranty2 {
                    margin-top: 20px;
                }
            }
        }
        &-docs {
            .pdp-downloads {
                margin-top: 0;
            }
        }
        &-cta {
            padding: 30px 0;
            .btn {
                padding: 0 15px;
                + .btn {
                    margin-left: 5px;
                }
            }
        }
    }
    &-preorder{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        border: 1px solid $c-white;
        color: $preorder-text-color;
        height: 40px;
        width: 100%;
        margin-top: 15px;
        &-info{
            display: flex;
            align-items: flex-start;
            margin-top: 15px;
            border-top: 1px solid $c-pdp-border;
            border-bottom: 1px solid $c-pdp-border;
            padding: 10px 0;
            .icon{
                margin-right: 10px;
                flex: 0 0 32px;
            }
            span{
                @include font-set(body-small);
                line-height: 18px;
            }
        }
    }
    &-badge3{
        display: flex;
        align-items: center;
        figure{
            flex: 0 0 30px;
            margin: 0 5px 0 -5px;
        }
        span{
            @include font-set(body-small);
        }
    }
}

