// Image Comparison Slider
.cocoen {
    cursor: pointer;
    line-height: 0;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: relative;
    user-select: none;
  
    img,
    video {
      max-width: none;
    }
  
    > img,
    > video {
      display: block;
      width: 100%;
    }

    .cocoen-title{
        color: $c-text-light;
        @include font-set(body-large);
        position: absolute;
        bottom: 10px;
    }

    > .cocoen-title{
        right: 10px;
        z-index: $z-lower;
    }

    .cocoen-first .cocoen-title{
        left: 10px;
    }
  
    .cocoen-first {
        height: 100%;
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 50%;
        z-index: $z-low;
      img,
      video {
        height: 100%;
      }
    }
  
    &-drag {
      display: inline-block;
      width: 2px;
      height: 100%;
      background: $c-white;
      cursor: ew-resize;
      position: absolute;
      left: 50%;
      top: 0;
      margin-left: -1px;
      z-index: $z-low;
  
      &::before{
        content: "";
        display: inline-block;
        @extend .svg;
        @extend .svg-cocoen;
        width: 66px;
        height: 66px;
        position: absolute;
        top: calc(50% - 33px);
        right: -32px;
      }
    }
  }

// Rating Bars
$rating-bars: ( 
    '1-5': 5, 
    '5-20': 20, 
    '20-50': 40,
    '50-75': 65, 
    '75-95': 85, 
    '100': 100
);
[data-bar-size]{
    font-size: 0;
    &::before{
        content: "";
        display: inline-block;
        font-size: 0;
        height: 100%;
        width: 0;
        background-color: $c-warning;
    }
}
@each $bar, $size in $rating-bars{
    [data-bar-size="#{$bar}"]{
        &::before{
            width: #{$size + '%'};
        }
    }
}

