            padding: 0 30px;
            margin-bottom: 15px;
            @include font-set(body-small);
        }
        .swiper-wrapper{
            padding: 1px 0;
        }
        .swiper-slide{
            width: 190px;
            padding: 0 5px;

            &:first-child{
                margin-left: 15px;
            }

            &:last-child{
                margin-right: 15px;
            }
        }

        .swiper-pagination, [class*="swiper-button-"]{
            display: none !important;
        }
    }
    &-selections{
        margin-top: 50px;

        &-title{
            @include font-set(h);
            margin-bottom: 20px;
            text-align: center;
        }
        .prd-row-inner{
            border-bottom: 1px solid $c-grey-border;
        }
    }
    &-current{
        .prd-row{
            background-color: $c-bg-body;
            &-inner{
                border-bottom: none;
            }
        }
    }
    &-bonus{
        .prd-row-info{
            color: $c-primary;
            font-weight: $fw-bold;
            margin-top: 5px;
        }
    }
    &-sum{
        display: flex;
        padding: 0 20px;
        margin-top: 30px;

        &-left{
            flex: 1;
            .promo-sum-title{
                @include font-set(body-small);
                margin-bottom: 10px; 
            }
        }

        &-prices{
            .prc-first{
                @include font-set(body-large);
                margin-right: 10px;
                color: $c-grey-dark;
            }

            .prc-last{
                @include font-set(h-paragraph);
            }
        }

        &-right{
            flex: none;
            .btn{
                padding: 0 20px;
            }
        }
    }
    &-conditions{
        background-color: $c-bg-body;
        padding: 50px 0 80px;

        h6{
            @include font-set(h-small);
            margin-bottom: 20px;
        }
    }
    &-products{
        margin: 0 -20px;
        #product-swiper{
            margin-bottom: 30px;
        }
        #product-swiper>.swiper-wrapper>.swiper-slide:first-child{
            margin-left: 20px;
        }
        #product-swiper>.swiper-wrapper>.swiper-slide:last-child{
            margin-right: 20px;
        }
    }
    &-row{
        margin-top: 30px;
    }
}

.page-promotion-detail{
    .promo{
        &-holder{
           position: relative;
           z-index: $z-low;
        }
        &-header{
            position: relative;
            top: -50px;
        }
        &-tab-contents{
            margin-top: -35px;
        }
    }

    #site-footer{
        margin-top: 0;
    }
}

// Built-in Set
.set{
    &-item{
        + .set-item{
            margin-top: 60px;
        }
    }
    &-title{
        padding: 15px 20px 10px;
        background-color: $c-bg-light;
        @include font-set(h-small);
    }
    &-media{
        margin-bottom: 20px;
        @include responsive(1200, 679);
    }
    &-products{
        #product-swiper{
            margin-bottom: 30px;
        }
        #product-swiper>.swiper-wrapper>.swiper-slide:first-child{
            margin-left: 20px;
        }
        #product-swiper>.swiper-wrapper>.swiper-slide:last-child{
            margin-right: 20px;
        }
    }
    &-sum{
        display: flex;
        align-items: center;
        padding: 0 20px;
        &-left{
            flex: 1;
        }
        &-title{
            @include font-set(body-small);
            margin-bottom: 10px;
        }
        &-price{
            .prc{
                @include font-set(body-large);
            }
            .prc-first{
                margin-right: 10px;
                color: $c-grey-dark;
            }
            .prc-last{
                font-weight: $fw-bold;
            }
        }
    }
    &-more{
        text-align: center;
        margin-top: 60px;
    }
}

// Assistant Page
#assistant-wizard{
    background-color: $c-bg-colored;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: calc(100vh - #{$header-height-mobile});
    // States
    &.asw-ready{
        .asw-enter{
            display: block;
        }
        .btn-apply{
            display: none;
        }
    }
    &.asw-completed{
        .btn-asw-next{
            display: none;
        }
    }
}

.asw{
    &-header{
        position: relative;
        flex: 1;
        padding-top: 125px;
    }
    &-media{
        width: 250px;
        position: absolute;
        z-index: $z-lower;
        top: -175px;
        left: calc(50% - 125px);
    }
    &-enter{
        width: 66%;
        max-width: 340px;
        margin: 0 auto;
        @include font-set(h);
        text-align: center;
        display: none;
    }
    &-step{
        display: none;
        &-info{
            padding: 0 20px 20px;
            display: flex;
            align-items: flex-end;
        }
        &-selections{
            height: 183px;
            .swiper-slide{
                width: 145px;
                + .swiper-slide{
                    margin-left: 5px;
                }
                &:first-child{
                    margin-left: 20px;
                }
                &:last-child{
                    margin-right: 20px;
                }
            }
            .rl-box{
                height: 100%;
                label{
                    background-color: rgba($c-bg-light, 0.6) !important; 
                }
                input{
                    &:checked + label{
                        background-color: $c-bg-light !important;
                    }
                }
            }
            .rl-text{
                margin: 2px -10px 0;
                text-align: center;
                justify-content: center;
                height: 36px;
                overflow: hidden;
                display: flex;
                align-items: center;
                @include font-set(body-small);
            }
        }
        &.asw-current{
            display: block;
        }
    }
    &-footer{
        padding: 30px 20px;
    }
    &-button{
        height: 44px;
        display: flex;
        align-items: center;
        .btn{
            margin-left: auto;
        }
        .link-inline{
            margin-left: 10px;
        }
    }
    &-selecteds{
        text-align: right;
        position: absolute;
        right: 20px;
        bottom: 20px;
        z-index: $z-low;
        li{
            overflow: hidden;
            height: 0;
            @include transitions($dur: 'now');
            button{
                opacity: 0;
                @include transitions($dur: 'faster');
            }
            + li.active{
                margin-top: 10px;
            }
            &.active{
                height: 40px;
                button{
                    opacity: 1;
                }
            }
        }
        button{
            display: inline-flex;
            height: 40px;
            align-items: center;
            position: relative;
            background-color: $c-white;
            border-radius: 20px;
            padding: 0 4px 0 20px;
            &::after{
                content: "";
                display: inline-block;
                width: 36px;
                height: 32px;
                @extend .svg;
                @extend .svg-remove;
                margin-left: 10px;
            }
        }
    }
}

#assistant.asr-cover{
    .bnr{
        &-body{
            padding: 60px 60px 40px;
        }
        &-media{
            bottom: auto !important;
            top: -60%;
            width: 250px !important;
            left: calc(50% - 125px) !important;
        }
        &-header{
            margin-bottom: 30px;
        }
        &-button{
            .btn{
                max-width: 180px;
                display: flex;
                margin: 0 auto 20px;
            }
        }
    }
}

// Call Me Back Form
.cmb{
    background-color: $c-bg-light;
    &-inner{
        max-width: 1440px;
        padding: 40px 0 30px;
        margin: 0 auto;
        position: relative;
    }
    &-close{
        position: absolute;
        top: 10px;
        right: 10px;
    }
    &-title{
        @include font-set(h);
    }
    &-form{
        margin-top: 20px;
        [class*="form-col-"]{
            padding: 0;
        }
        .form-button{
            text-align: right;
        }
    }
}

// Meeting Starter Box
.msb{
    position: relative;
    margin: 20px 0;
    border: 2px solid $c-primary;
    border-radius: 5px;
    &-inner{
        display: flex;
        max-width: 850px;
        margin: 0 auto;
    }
    &-media{
        width: 100px;
        flex: none;
        position: relative;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: $z-zero;
            background: url(#{$dir-images + 'arcelik-yaprak.svg'}) center right no-repeat;
            background-size: cover;
            opacity: .03;
        }
        figure{
            display: inline-block;
            position: relative;
            z-index: $z-lower;
            &::after{
                content: "";
                display: block;
                width: 14px;
                height: 14px;
                border-radius: 50%;
                border: 1px solid $c-white;
                background-color: $c-success;
                position: absolute;
                bottom: 7px;
                right: -5px;
                z-index: $z-low;
            }
        }
    }
    &-body{
        padding: 10px 50px 10px 15px;
    }
    &-button{
        margin-top: 10px;
    }
    &-text{
        font-weight: $fw-bold;
    }
    .btn-msb-close{
        position: absolute;
        top: 8px;
        right: 8px;
    }
}

// Coupon Counter
.ccm{
    background-color: $c-bg-light;
    &-inner{
        max-width: 1440px;
        padding: 40px 0 30px;
        margin: 0 auto;
        position: relative;
    }
    &-body{
        max-width: 700px;
    }
    &-close{
        position: absolute;
        top: 10px;
        right: 10px;
    }
    &-title{
        @include font-set(h);
    }
    &-content{
        display: none;
        margin: 20px 0;
    }
    &-revealer{
        margin: 20px 0;
    }
    &-timer{
        flex: 1;
        .time{
            @include font-set(h);
            .digit{
                display: inline-block;
                width: 30px;
                text-align: center;
            }
            .sep{
                animation: blink-animation 1s steps(5, start) infinite;
                padding: 0;
            }
        }
    }
    &-code{
        flex: 1;
        position: relative;
        .copy-code{
            position: absolute;
            right: 0;
            bottom: 8px;
            .icon{
                color: $c-primary;
            }
        }
    }
    &-info{
        @include font-set(body-small);
    }
    &.show-coupon{
       .ccm-content{
           display: flex;
       }
       .ccm-revealer{
            display: none;
        }
    }
}

.menu-ready{
    .pdp-purchase-fixed{
        display: none !important;
    }
}

// PDP Recipies
.apr{
    display: flex;
    justify-content: space-between;
    background-color: $c-bg-body;
    &-body{
        padding: 20px 0 20px 15px;
        display: flex;
        flex-direction: column;
    }
    &-title{
        @include font-set(h-tiny);
        color: $c-primary;
        margin-bottom: 2px;
    }
    &-desc{
        max-width: 265px;
        @include font-set(body-small);
        line-height: 1.3;
    }
    &-cta{
        margin-top: 25px;
    }
    &-media{
        @include responsive(213, 169);
        flex: 0 0 156px;
        display: flex;
        align-items: center;
    }
}