@include d-elements-appshell;

// Mobile Scrollable
.scrollable-h{
    white-space: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    > * {
        white-space: normal;
    }

    &::-webkit-scrollbar{
        display: none;
    }
}

// Scrollable Area
.scrollable-area {
    overflow: auto;
    padding-bottom: 10px;
    width: 100%;
}

